import React from 'react'
import {useHistory} from 'react-router-dom'

function WorkItem(props) {

    const history = useHistory();
    


    return (
        <div className="item">
                    <img src={props.img} alt="" width="450px" height="337px" />
                    <h4>{props.title}</h4>

                     <ul style={{justifyContent: "space-around"}}>
                         <li style={{textAlign: "center"}}><img src="media/arm.svg" alt=""/> <br/> <br/>
                         <span>{props.level}</span> </li>
                         <li style={{textAlign: "center"}}><img src="media/clocks.svg" alt=""/> <br/> <br/>
                         <span>{props.min} min</span></li>
                     </ul>
                    
                     <a onClick={() => history.push(`#/workout/${props.getId}`)} href={`#/workout/${props.getId}`}> <button style={{backgroundColor:"lightsteelblue"}}>How to do it...</button></a>
                     
        </div>
    )
}

export default WorkItem
