import React from 'react'
import './mealsDetail.css'
function MealsDetail(props) {

    let show = false;

    if(props.title === "Banana Cake"){
        show = true
    } else {
        show = false;
    }

    return (
        <div className="meals-detail">

            <h1>{props.title}</h1>
            <div className="body-detail">
                <div className="cook">
                    <div className="ingerdients">
                        <img src={props.img} width="410px" height="355px" alt=""/>
                        <h2>Ingerdients</h2>
                        <ul>
                            {props.items}
                        </ul>
                    </div>

                    <div className="intstruction">
                        <img src="media/meals/cook.gif" width="410px" height="355px" alt=""/>
                        <h2>Instructions</h2>
                        <ul>
                            {props.instruction}
                        </ul>
                    </div>
                </div>

                <div className="note" style={{display: !show && 'none'}}>
                        {props.notes}
                </div>

                    <iframe src={props.ved} width="1000px" height="450px" frameBorder="0" title="myFrame" />
            </div>
        </div>
    )
}

export default MealsDetail
