import React,{useState} from 'react'
import './header.css'
import {useHistory} from 'react-router-dom'
function Header() {

    const [show, setShow] = useState(true);
    const history = useHistory();

    return (
        <div className="header">
            <div className={show ? "container" : "container1"}>
                <div className="left">
                    <h2 className="title">Try to Healthy</h2>
                    <img width="200px" src="media/healthylife.png" alt=""/>
                </div>

                <div className="right">
                        <a onClick={() => history.push(`#/home`)} href="#/home"> <button>HOME</button></a>
                        <a onClick={() => history.push(`#/workout`)} href="#/workout"> <button>WORKOUT</button></a>
                        <a onClick={() => history.push(`#/meals`)} href="#/meals"> <button>MEALS</button></a>
                        <a onClick={() => history.push(`#/about`)} href="#/about"><button>ABOUT</button></a>
                        <button className="bars" style={{color: "red"}} onClick={() => setShow(false)}><img width="50px" src="/media/barss.svg" alt=""/></button>
                        <button className="close" onClick={() => setShow(true)} ><img width="50px" src="/media/close.svg" alt=""/></button>
                </div>
            </div>
        </div>
    )
}



export default Header
