import React from 'react'
import './workDetail.css'
import Marquee from './Marquee.jsx'
function Detail(props) {

return (
<div className="detail">

    <div className="detail-body">
        <div className="left">
            <h2>{props.name}</h2>
            <p>{props.define}</p>
            <br />

            <ul>
                <li><img src="media/arm.svg" alt="" /> <br />
                    <span>{props.level}</span> </li>
                <li><img src="media/clocks.svg" alt="" /> <br />
                    <span>{props.min} min</span></li>
            </ul>
        </div>

        <div className="right">
            <img src={props.img} width="325px" height="325px" alt="" />
        </div>

    </div>
    <div className="exercise">

        <h2>{props.name1} Exercises</h2>
        <ul>
            {props.exercise}
        </ul>

    </div>

        <div className="marqueeWork">
            <Marquee 
            paragraph = {props.motivate}
            />
        </div>

    <div className="video">
            {props.youtube}
    </div>

</div>
)
}

export default Detail;