import React from 'react'
import './meals.css'
import Header from './views/Header.jsx'
import MealsItem from './MealsItem.jsx'
import item from './item.js'
import {HashRouter as Router,
Switch,
Route,
} from "react-router-dom";
import mealsDetail from './mealsDetail.js'
import MealsDetail from './MealsDetail.jsx'
import Footer from './views/Footer.jsx'
function Meals() {

return (
<div className="meal">
    <header>
        <Header />
    </header>


    <Router>
        <Route path="/meals" exact render={()=>
            <div className="meals-body">
                <div className="text">
                    <h2>How Many Calories Do You Need?</h2>
                    <br />
                    <p>
                        The easiest way to determine how many calories you need is to weigh yourself at least three
                        times a week and record what you eat using a calorie tracking app.
                        <br />
                        If your weight stays the same, the daily number of calories you eat is your maintenance calories
                        — in other words, you’re not losing or gaining weight, but maintaining it.
                        <br />
                        During your bulking phase, it’s recommended to increase your calorie intake by 15%. For example,
                        if your maintenance calories are 3,000 per day, you should eat 3,450 calories per day (3,000 x
                        0.15 = 450) during your bulking phase.
                        <br />
                        When transitioning from a bulking to a cutting phase, you would instead decrease your
                        maintenance calories by 15%, meaning you would eat 2,550 calories per day instead of 3,450.
                        <br />
                        As you gain weight in the bulking phase or lose weight in the cutting phase, you will need to
                        adjust your calorie intake at least monthly to account for changes in your weight.
                        <br />
                        Increase your calories as you gain weight in the bulking phase and decrease your calories as you
                        lose weight in the cutting phase for continued progression.
                        <br />
                        During either phase, it’s recommended not to lose or gain more than 0.5–1% of your body weight
                        per week. This ensures that you don’t lose too much muscle during the cutting phase or gain too
                        much body fat during the bulking phase.
                    </p>
                </div>

                <br />
                <br />

                <div className="choice">
                    <h3>Here's some food help you to a good body</h3>
                    <img src="media/Choice.gif" alt="Choice" />
                </div>

                <div className="list-items">

                    {item.map((list) =>(
                    <MealsItem 
                    key = {list.id}
                    src={list.img} 
                    title={list.title} 
                    food={list.food} 
                    bicycle={list.bicycle}
                    min={list.min} 
                    getId={list.id} />
                    ))}

                </div>
            </div>

            }/>
            
            <Switch>
            {mealsDetail.map((items)=> (
                <Route key={items.id} path={`/meals/${items.id}`} render={()=>(
                    <MealsDetail
                        key={items.id}
                        title={items.title} 
                        items={items.array.map((item, index)=>(
                        <li key={index}>{item}</li>
                        ))}
                        
                        instruction = {items.instruction.map((item, index) =>(
                        <li key={index}>{item}</li>
                        ))}
                        
                        notes = {items.id === 5 && items.cream.map((notes, index) =>{
                            return <ul key={index} className="note-ul">
                                    {index === 0 && <h2>Notes</h2>}
                                    <li key={index}>{notes}</li>
                                   </ul>
                        })}

                        ved = {items.vedio}
                        img ={items.img}
                    />
                )}
                />
            ))}
            </Switch>


        <footer>
            <Footer />
        </footer>
    </Router>

</div>
)
}

export default Meals