import React from 'react'
import { useHistory } from 'react-router-dom'

function Item(props) {

    const history = useHistory();
    return (
            <div className="item">
                       <img src={props.src} alt="" width="450px" height="337.5" />
                       <h4>{props.title}</h4>
                       <ul>
                           <li><img src="media/dinner.svg" alt=""/> <br/><br/>
                           <span>{props.food}</span></li>
                           <li><img src="media/fitness.svg" alt=""/><br/><br/>
                           <span>{props.bicycle}</span></li>
                           <li><img src="media/clocks.svg" alt=""/><br/><br/>
                           <span>{props.min} min</span></li>
                       </ul>
                       <a onClick={() => history.push(`#/meals/${props.getId}`)} href={`#/meals/${props.getId}`}> <button>More Detail...</button></a>
            </div>
    )
}

export default Item
