import React from 'react'
import Header from './views/Header.jsx'
import WorkItem from './workItem.jsx'
import {workout} from './item.js'
import './meals.css'
import {HashRouter as Router,
    Switch,
    Route,
  } from "react-router-dom";
import WorkDetail from './WorkDetail.jsx'
import workDetail from './workDetail.js'
import Footer from './views/Footer.jsx'


  function Workout() {

    return (
        <div className="meal">
            <header>
                 <Header />
            </header>

            <Router>
             <Route path="/workout" exact render={() => 
            <div className="meals-body">
                <div className="list-items">
                    {workout.map((list, index) =>(
                        <WorkItem
                        key = {index}
                        img = {list.img}
                        title = {list.title}
                        level = {list.level}
                        min = {list.min}
                        getId = {list.id}
                        />
                    ))}
                </div>
            </div>
            } />

            <Switch >
                {workDetail.map((items, index) =>(
                    <Route key={index} path={`/workout/${items.id}`} render={() => 

                    <WorkDetail
                    key = {index}
                    name = {items.name}
                    define = {items.define}
                    level = {items.level}
                    min = {items.min}
                    img = {items.img}
                    name1 = {items.nameE}
                    exercise ={
                        items.exercise.map((exe, index) =>(
                            <li key={index}>{exe}</li>
                        ))
                    }
                    e1 = {items.e1}
                    e2 = {items.e2}
                    e3 = {items.e3}
                    motivate = {items.motivate}
                    youtube= {items.vedio.map((ved, index) =>(
                        <iframe key={index} width="1000px" height="450px" frameBorder="0" src={ved} title="myFrame" />
                    ))}
                    />

                    }
                    />
                ))}

            </Switch>

            
        </Router>

        <footer>
            <Footer />
        </footer>
        </div>
    )

}

export default Workout
