import React from 'react'
import './main.css';
import {useHistory} from 'react-router-dom';

function Main() {

  const history = useHistory();
  
  return (
    <div className="main">
      <img src="media/main/main.jpg" alt=""/>
      <h2>Don't wish for IT  work for IT</h2>
      <a onClick={() => history.push(`#/home`)} href="#/home" rel="noreferrer">
            <button>Get It</button>
      </a>
    </div>
  );
}

export default Main;